"use client";
import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import styles from './Header.module.css';

interface User {
  username: string;
  email: string;
}

export default function Header() {
  const router = useRouter();
  const [showProfileMenu, setShowProfileMenu] = useState(false);
  const [currentUser, setCurrentUser] = useState<User | null>(null);

  useEffect(() => {
    // Get the current user from localStorage
    const getCurrentUser = () => {
      const userEmail = localStorage.getItem('currentUserEmail');
      if (userEmail) {
        const users = JSON.parse(localStorage.getItem('users') || '[]');
        const user = users.find((u: User) => u.email === userEmail);
        if (user) {
          setCurrentUser(user);
        } else {
          // If user not found, redirect to login
          router.push('/login');
        }
      } else {
        // If no current user email, redirect to login
        router.push('/login');
      }
    };

    getCurrentUser();
  }, [router]);

  const handleLogout = () => {
    // Clear the current user email from localStorage
    localStorage.removeItem('currentUserEmail');
    router.push('/login');
  };

  // If user is not loaded yet, you might want to show a loading state
  if (!currentUser) {
    return null; // Or return a loading spinner
  }

  return (
    <header className={styles.header}>
      <div className={styles.headerContent}>
        <div className={styles.leftSection}>
          <h1 className={styles.logo}>Gamification</h1>
          <nav className={styles.navigation}>
            {/* <Link href="/dashboard" className={styles.navLink}>Dashboard</Link> */}
            <Link href="/tasks" className={styles.navLink}>Tasks</Link>
            {/* <Link href="/calendar" className={styles.navLink}>Calendar</Link> */}
          </nav>
        </div>
        
        <div className={styles.rightSection}>
          <div className={styles.searchBar}>
            <input 
              type="search" 
              placeholder="Search tasks..." 
              className={styles.searchInput}
            />
            <button className={styles.searchButton}>
              üîç
            </button>
          </div>
          
          <div className={styles.userSection}>
            <button className={styles.notificationButton}>
              üîî
              <span className={styles.notificationBadge}>3</span>
            </button>
            <div className={styles.userProfile}>
              <button 
                className={styles.profileButton}
                onClick={() => setShowProfileMenu(!showProfileMenu)}
              >
                <div className={styles.avatar}>
                  üë§
                </div>
                <div className={styles.userInfo}>
                  <span className={styles.userName}>{currentUser.username}</span>
                  <span className={styles.userRole}>Premium User</span>
                </div>
              </button>
              
              {showProfileMenu && (
                <div className={styles.profileMenu}>
                  <Link 
                    href="/profile" 
                    className={styles.menuItem}
                    onClick={() => setShowProfileMenu(false)}
                  >
                    <span className={styles.menuIcon}>üë§</span>
                    My Profile
                  </Link>
                  <button 
                    className={styles.menuItem}
                    onClick={handleLogout}
                  >
                    <span className={styles.menuIcon}>üö™</span>
                    Logout
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </header>
  );
} 